@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;900&display=swap');

*,
*::before,
*::after {
	box-sizing: inherit;
}

html, body {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Raleway', sans-serif;
	font-weight: normal;
}

.container {
	max-width: 1280px;
	margin: 0 auto;
	padding-left: 40px;
	padding-right: 40px;
}

.flex {
	display: flex;
	flex-wrap: wrap;
}

section {
	width: 100%;
	min-height: 100vh;
	//min-height: 900px;
}

.section {
	&__first-screen {
		background-color: $color-black;
	}

	&__second-screen {
		background-color: $color-yellow;
		padding-top: 2em
	}
}

.first-screen__text {
	font-size: 3em;
	font-weight: 900;
	width: 80%;
	position: relative;

	&:nth-child(1) {
		color: $color-yellow;
		padding: 2em 0 1em;
	}
	&:nth-child(2) {
		color: $color-white;
		padding: 0 0 1em;
	}

	&::before {
		position: absolute;
		height: 10px;
		width: 50px;
		background-color: $color-white;
		top: 0;
		left: 0;
		z-index: -1;
	}
}

.block {
	width: 30%;
	margin: 20px auto;
	background-color: $color-white;

	&__title {
		font-size: 1.75em;
		font-weight: 900;
		padding: .25em;
		background-color: $color-black;
		color: $color-white;
		min-height: 78px;
	}

	&__text {
		font-size: 1.25em;
		padding: 1em 2em 2em;
	}

}

.third-screen__slogan {
	width: 30%;
	font-size: 3em;
	color: $color-black;
	font-weight: 900;
	padding-top: 2em;
}

.form__contact {
	width: 70%;
	display: flex;
	flex-direction: column;

	input, textarea {
		margin-top: 20px;
		width: 100%;
		height: 60px;
		//border-radius: 5px;
		border: 0;
		outline: 0;
		padding-left: 20px;
		border-bottom: 2px solid #ccc;
		background-color: #f5f3f3;

		&::placeholder {
			font-size: 18px;
			color: #ccc;
		}
	}

	textarea {
		height: 150px;
		padding-top: 20px;
	}

	input[type="submit"] {
		max-width: 310px;
		margin: 50px auto 0;
		font-weight: 800;
		font-size: 18px;
		color: $color-white;
		background-color: $color-black;

		&:hover {
			cursor: pointer;
			background-color: #ccc;
			text-decoration: none;
		}
	}
}
