*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

.is-hidden {
  display: none !important; }

@media (max-width: 1024px) {
  .for-desktop {
    display: none !important; } }

@media (min-width: 1025px) {
  .for-mobile {
    display: none !important; } }

.clearfix {
  overflow: auto; }
  .clearfix::after {
    content: "";
    display: table;
    clear: both; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  font-weight: normal; }

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px; }

.flex {
  display: flex;
  flex-wrap: wrap; }

section {
  width: 100%;
  min-height: 100vh; }

.section__first-screen {
  background-color: #000; }

.section__second-screen {
  background-color: #ffc40b;
  padding-top: 2em; }

.first-screen__text {
  font-size: 3em;
  font-weight: 900;
  width: 80%;
  position: relative; }
  .first-screen__text:nth-child(1) {
    color: #ffc40b;
    padding: 2em 0 1em; }
  .first-screen__text:nth-child(2) {
    color: #fff;
    padding: 0 0 1em; }
  .first-screen__text::before {
    position: absolute;
    height: 10px;
    width: 50px;
    background-color: #fff;
    top: 0;
    left: 0;
    z-index: -1; }

.block {
  width: 30%;
  margin: 20px auto;
  background-color: #fff; }
  .block__title {
    font-size: 1.75em;
    font-weight: 900;
    padding: .25em;
    background-color: #000;
    color: #fff;
    min-height: 78px; }
  .block__text {
    font-size: 1.25em;
    padding: 1em 2em 2em; }

.third-screen__slogan {
  width: 30%;
  font-size: 3em;
  color: #000;
  font-weight: 900;
  padding-top: 2em; }

.form__contact {
  width: 70%;
  display: flex;
  flex-direction: column; }
  .form__contact input, .form__contact textarea {
    margin-top: 20px;
    width: 100%;
    height: 60px;
    border: 0;
    outline: 0;
    padding-left: 20px;
    border-bottom: 2px solid #ccc;
    background-color: #f5f3f3; }
    .form__contact input::placeholder, .form__contact textarea::placeholder {
      font-size: 18px;
      color: #ccc; }
  .form__contact textarea {
    height: 150px;
    padding-top: 20px; }
  .form__contact input[type="submit"] {
    max-width: 310px;
    margin: 50px auto 0;
    font-weight: 800;
    font-size: 18px;
    color: #fff;
    background-color: #000; }
    .form__contact input[type="submit"]:hover {
      cursor: pointer;
      background-color: #ccc;
      text-decoration: none; }
